body {
  background-color: #000;
  height: 100vh;
  margin: 0;
  font-family: Copperplate, Papyrus, fantasy;
  background-image: url('https://images.pexels.com/photos/417074/pexels-photo-417074.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=2');
  background-repeat: repeat;
  background-position: center;
  background-size: cover;
}

nav {
  display: flex;
  backdrop-filter: blur(30px);
  -webkit-backdrop-filter: blur(3px);
  box-shadow: 0 15px 30px 0 #0000005e;
  justify-content: space-between;
  padding-right: 35px;
}

.bouncing-blob {
  width: 32vw;
  aspect-ratio: 1;
  border-radius: 50%;
  will-change: transform;
  position: absolute;
  z-index: 1;
  left: 0; 
  top: 0;
  transform-origin: left top;
}

.bouncing-blob--blue {
  background: #4783c7;
}

.bouncing-blob--light-blue {
  background: #00fffc;
  z-index: 2;
  width: 15vw;
}

.bouncing-blob--purple {
  background: #8c8ff1;
}

.bouncing-blob--pink {
  background: #ff13c850;
}

.bouncing-blobs-container {
  position: fixed;
  z-index: -1;
  top: 0; 
  left: 0;
  width: 100%;
  height: 100%;
}

.bouncing-blobs-glass {
  position: absolute;
  z-index: 2;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  backdrop-filter: blur(50px);
  -webkit-backdrop-filter: blur(50px);
  pointer-events: none;
}

.bouncing-blobs {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.hero {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}

@media (max-width: 1200px) {
  .bouncing-blobs-glass {
    backdrop-filter: blur(50px);
    -webkit-backdrop-filter: blur(50px);
  }
}

@media (max-width: 500px) {
  .bouncing-blob {
    width: 60vw;
  }

  .bouncing-blob--white {
    width: 30vw;
  }

  .bouncing-blobs-glass {
    backdrop-filter: blur(50px);
    -webkit-backdrop-filter: blur(50px);
  }
}


